<template>
<div class="navbar navbar-light bg-faded navigation">
  <a class="nav-link btn btn-primary-outline btn-sm" @click="GoBack">
    <i class="fa fa-arrow-left"></i> 返回
  </a>
  <slot name="title"></slot>
  <slot name="right-content"></slot>
</div>
</template>
<script>
  export default {
    methods: {
      GoBack () {
        this.$route.router.go(window.history.back())
      }
    }
  }
</script>



